@import 'colours';

.nav {
    width: 100%;
    height: $header-height;
    display: flex;
    justify-content: space-between;
    background-color: $gray-scale-color04;
    align-items: center;
    z-index: 999;
    box-shadow: 0px 5px 5px 0px #00000088;
    position: fixed;
}

@media (min-width: 600px) {
    .nav-banner {
        width: 30%;
    }

    .nav-block {
        width: 30%;
    }

    .nav-banner {
        display: grid;
        grid-template-columns: 0.25fr 1fr;
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        height: 100%;
    }

    .nav-banner h3, .nav-banner p {
        margin: auto 0;
    }
}
@media (max-width: 600px) or (orientation: portrait) {
    .nav-banner {
        width: 20%;
    }

    .nav-block {
        width: 80%;
    }

    .nav-banner {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .nav-banner h3, .nav-banner p {
        display: none;
    }
}



.nav-block {
    color: $white;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-banner h3, .nav-banner p {
    margin: auto 0;
}

#logo {
    margin: auto;
    width: var(--header-height);
    height: var(--header-height);
    grid-area: 1 / 1 / 3 / 2;
    width: 55px;
    height: 55px;
}

ul {
    list-style: none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}

li {
    font-size: 1.2rem;
    cursor: pointer;

    i {
        font-size: 2rem;
        cursor: pointer;
    }
}

a {
    text-decoration: none;
    height: 100%;
    width: calc(100% / 5);
    text-align: center;
    justify-content: center;
    display: flex;
    cursor: pointer;
    align-items: center;
    transition: background-color 0.4s ease;
    padding: 0.5rem;
}

a:hover {
    background-color: $gray-scale-color02;
}