@import 'colours';

@media (min-width: 600px) {
    body {
        width: calc(100vw - $scroll-bar);
        background-color: $white;
    }
}
@media (max-width: 600px) or (orientation: portrait){
    body {
        width: 100vw;
        background-color: $white;
        overflow-y: scroll;
    }
}


.root {
    width: 100%;
    height: 100%;
}