@import 'colours';

//Navegadores
@media (min-width: 600px) {
    .comment-container {
        display: grid;
        align-items: center;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);
        grid-column-gap: 5px;
        grid-row-gap: 5px;
    }

    .comment-target {
        margin: auto;
    }

    .comment-title {
        grid-area: 3 / 1 / 4 / 2;
        width: 100%;
        font-size: 3rem;
        text-overflow: clip;
        text-align: center;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
    }
}

@media (max-width: 600px) or (orientation: portrait) {
    .comment-container {
        display: flex;
        flex-direction: column-reverse;
        box-shadow:  0px 5px 5px 0px #00000088;;
    }

    .comment-target {
        margin: 0.5rem;
    }

    .comment-title {
        grid-area: 3 / 1 / 4 / 2;
        width: 100%;
        font-size: 2.3rem;
        text-overflow: clip;
        text-align: center;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
    }
}

@media (max-width: 600px) or (orientation: portrait) {

}

.comment-container {
    width: 100%;
    height: 100%;
    padding: 1rem;
    box-shadow: 0px 5px 5px 0px #00000088;
}



.comment-target {
    width: 97.5%;
    height: 97.5%;
    background-color: $gray-scale-color05;
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    grid-template-rows: 0.5fr 1fr;
    grid-column-gap: 3px;
    grid-row-gap: 3px;
    transition: transform 0.5s ease;
    box-shadow: 0px 5px 5px 0px #00000088;
}

.comment-target:hover {
    transform: scale(1.025);
}

.target-img {
    grid-area: 1 / 1 / 3 / 2;
    width: 90%;
    height: 90%;
    margin: auto;
}

.target-name {
    margin: auto;
    font-size: 1.2rem;
}