// COLORS, FONTS AND SELECTORS
@import url('https://fonts.googleapis.com/css2?family=Roboto+Serif:ital,opsz,wght@0,8..144,100;0,8..144,200;0,8..144,300;0,8..144,600;0,8..144,700;0,8..144,800;0,8..144,900;1,8..144,100;1,8..144,200;1,8..144,700&display=swap');

$gray-scale-color01:#232323;
$gray-scale-color02:#535353;
$gray-scale-color03:#888888;
$gray-scale-color04:#c2c2c2;
$gray-scale-color05:#d2d2d2;

$black: #000000;
$white:#ffffff;

$facebook-btn: #3b5998;
$instagram-btn: #125688;
$whatsapp-btn: #4dc247;
$twitter-btn: #55acee;
$linkdin-btn: #007bb5;

$facebook-hbtn: #3b5998aa;
$instagram-hbtn: #125688aa;
$whatsapp-hbtn: #4dc247aa;
$twitter-hbtn: #55aceeaa;
$linkdin-hbtn: #007bb5aa;

$header-height: 60px;
$scroll-bar: 10px;

$principal-font: 'Roboto Serif', serif;

* {
    margin: 0;
    box-sizing: border-box;
    color: $black;
    font-family: $principal-font;
    cursor: default;
}
//Navegadores
@media (min-width: 600px) {
    ::-webkit-scrollbar {
        width: $scroll-bar;
    }

    ::-webkit-scrollbar-track {
        background: $gray-scale-color04;
    }

    ::-webkit-scrollbar-thumb {
        background: $gray-scale-color01;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: $black;
    }
}

html {
    scroll-behavior: smooth;
}