@import 'colours';

@media (min-width: 600px) {

    .law-capsule-container {
        width: 100%;
        height: 100%;
        padding: 1rem;
        display: grid;
        align-items: center;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 1fr 5fr 1fr;
        grid-column-gap: 1rem;
        grid-row-gap: 1rem;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .law-capsule-title {
        grid-area: 1 / 1 / 2 / 2;
        font-size: 3.5rem;
        text-align: center;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
    }
    
    .tracks-list {
        width: 100%;
        height: 100%;
        background-color: $gray-scale-color04;
        grid-area: 2 / 1 / 4 / 2;
        padding: 0.5rem;
        overflow-y: scroll;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .tracks-list::-webkit-scrollbar {
        width: 10px;
    }
    
    .tracks-list::-webkit-scrollbar-track {
        background: $white;
    }
    
    .tracks-list::-webkit-scrollbar-thumb {
        background: $gray-scale-color03;
    }
    
    .tracks-list::-webkit-scrollbar-thumb:hover {
        background: $gray-scale-color05;
    }
    
    .track-id {
        width: 95%;
        height: 20%;
        margin: 0.5rem auto;
        transition: background-color 0.4s ease;
        background-color: $gray-scale-color05;
        display: grid;
        align-items: center;
        grid-template-columns: 1fr 4fr;
        grid-template-rows: 1fr 2fr;
        grid-column-gap: 1rem;
        grid-row-gap: 3px;
    }
    
    .track-id:hover {
        background-color: $gray-scale-color03;
    }
    
    .track-img {
        grid-area: 1 / 1 / 3 / 2;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        background-color: #00000088;
    
        i {
            margin: auto;
            font-size: 1.5rem;
            cursor: pointer;
        }
    
        h4 {
            text-align: center;
        }
    
        p {
            width: 100%;
            height: 100%;
        }
    }
    
    .track-description {
        width: 100%;
        height: 100%;
        background-color: $gray-scale-color04;
        grid-area: 1 / 2 / 3 / 3;
        box-shadow: 0px 5px 5px 0px #00000088;
        padding: 2rem;
        text-align: justify;
        text-indent: 1rem;
        overflow-y: scroll;
    }

    .track-control {
        margin: auto;
        width: 80%;
        height: 100%;
        background-color: $gray-scale-color04;
        grid-area: 3 / 2 / 4 / 3;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding: 0.25rem;
        box-shadow: 0px 5px 5px 0px #00000088;
    }
}
@media (max-width: 600px) {

    .law-capsule-container {
        width: 100%;
        height: 100%;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .law-capsule-title {
        font-size: 2.5rem;
        text-align: center;
        padding: 1rem;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
    }

    .tracks-list {
        width: 100%;
        height: 20%;
        background-color: $gray-scale-color04;
        display: flex;
        padding: 0.5rem;
        overflow-x: scroll;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .tracks-list::-webkit-scrollbar {
        height: 2px;
    }

    .tracks-list::-webkit-scrollbar-track {
        background: $white;
    }
    
    .tracks-list::-webkit-scrollbar-thumb {
        background: $gray-scale-color03;
    }
    
    .tracks-list::-webkit-scrollbar-thumb:hover {
        background: $gray-scale-color05;
    }
    
    .track-id {
        width: 100%;
        height: calc(100% - 1rem);
        margin: 0.5rem 0.5rem;
        transition: background-color 0.4s ease;
        background-color: $gray-scale-color05;
    }
    
    .track-id:hover {
        background-color: $gray-scale-color03;
    }
    
    .track-img {
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        background-color: #00000088;
        padding: 0.2rem;
    
        i {
            margin: auto;
            font-size: 1.5rem;
            cursor: pointer;
        }
    
        .track-list-title, .track-list-description {
            width: 90%;
            margin: auto;
        }

        h4 {
            text-align: center;
            margin: 0 0.2rem;
        }
    
        p {
            display: none;
        }
    }
    
    .track-description {
        width: 100%;
        height: 50%;
        background-color: $gray-scale-color04;
        grid-area: 1 / 2 / 3 / 3;
        box-shadow: 0px 5px 5px 0px #00000088;
        padding: 2rem;
        text-align: justify;
        text-indent: 1rem;
        overflow-y: scroll;
        margin: 1rem 0;
    }

    .track-control {
        margin: auto;
        width: 100%;
        height: 10%;
        background-color: $gray-scale-color04;
        grid-area: 3 / 2 / 4 / 3;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding: 0.25rem;
        box-shadow: 0px 5px 5px 0px #00000088;
    }
}


// Aqui inicia

.track-control {

    h3 {
        text-align: center;
        height: 50%;
    }

    .track-control-buttons {
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        button {
            padding: 0;
            border: none;
            background: none;
            cursor: pointer;
            outline: none;
            width: 10%;
            height: 100%;
            text-align: center;
            font-size: 1.5rem;
        }

        #seek-slider {
            width: 40%;
        }

        #volume-slider {
            width: 20%;
        }

        #volume-slider::-webkit-slider-runnable-track {
            background-color: #fff;
        }

        #volume-slider::-moz-range-track {
            background-color: #fff;
        }

        #volume-slider::-ms-fill-upper {
            background-color: #fff;
        }

        input[type=range] {
            position: relative;
            -webkit-appearance: none;
            width: 48%;
            margin: 0;
            padding: 0;
            height: 19px;
            margin: 30px 2.5% 20px 2.5%;
            float: left;
            outline: none;
        }

        input[type=range]::-webkit-slider-runnable-track {
            width: 100%;
            height: 3px;
            cursor: pointer;
            background: linear-gradient(to right, rgba(0, 125, 181, 0.6) var(--buffered-width), rgba(0, 125, 181, 0.2) var(--buffered-width));
        }

        input[type=range]::before {
            position: absolute;
            content: "";
            top: 8px;
            left: 0;
            width: var(--seek-before-width);
            height: 3px;
            background-color: #007db5;
            cursor: pointer;
        }

        input[type=range]::-webkit-slider-thumb {
            position: relative;
            -webkit-appearance: none;
            box-sizing: content-box;
            border: 1px solid #007db5;
            height: 15px;
            width: 15px;
            border-radius: 50%;
            background-color: #fff;
            cursor: pointer;
            margin: -7px 0 0 0;
        }

        input[type=range]:active::-webkit-slider-thumb {
            transform: scale(1.2);
            background: #007db5;
        }

        input[type=range]::-moz-range-track {
            width: 100%;
            height: 3px;
            cursor: pointer;
            background: linear-gradient(to right, rgba(0, 125, 181, 0.6) var(--buffered-width), rgba(0, 125, 181, 0.2) var(--buffered-width));
        }

        input[type=range]::-moz-focus-outer {
            border: 0;
        }

        input[type=range]::-moz-range-thumb {
            box-sizing: content-box;
            border: 1px solid #007db5;
            height: 15px;
            width: 15px;
            border-radius: 50%;
            background-color: #fff;
            cursor: pointer;
        }

        input[type=range]:active::-moz-range-thumb {
            transform: scale(1.2);
            background: #007db5;
        }

        input[type=range]::-ms-track {
            width: 100%;
            height: 3px;
            cursor: pointer;
            background: transparent;
            border: solid transparent;
            color: transparent;
        }

        input[type=range]::-ms-fill-lower {
            background-color: #007db5;
        }

        input[type=range]::-ms-fill-upper {
            background: linear-gradient(to right, rgba(0, 125, 181, 0.6) var(--buffered-width), rgba(0, 125, 181, 0.2) var(--buffered-width));
        }

        input[type=range]::-ms-thumb {
            box-sizing: content-box;
            border: 1px solid #007db5;
            height: 15px;
            width: 15px;
            border-radius: 50%;
            background-color: #fff;
            cursor: pointer;
        }

        input[type=range]:active::-ms-thumb {
            transform: scale(1.2);
            background: #007db5;
        }

        span {
            width: 10%;
            text-align: center;
        }
    }
}
