@import 'colours';

@media (min-width: 600px){
    .contacts-container {
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 1fr 4fr 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        padding-top: 1rem;
    }

    .form-contact-container {
        width: 90%;
        height: 90%;
        background-color: $gray-scale-color05;
        grid-area: 1 / 2 / 3 / 3;
        margin: auto;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .social-media-buttons {
        width: 50%;
        height: calc(100% - 1rem);
        margin: 0.5rem auto;
        background-color: $black;
        grid-area: 3 / 2 / 4 / 3;
        display: flex;
        align-items: center;
        justify-content: space-around;
        border-radius: 0.5rem;
    }

    .contacts-container h2 {
        margin: auto;
        font-size: 3.5rem;
        grid-area: 1 / 1 / 2 / 2;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
        width: 75%;
        text-align: center;
    }
}
//Moviles
@media (max-width: 600px) or (orientation: portrait) {
    .contacts-container {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 1rem;
    }

    .form-contact-container {
        width: 100%;
        height: 40%;
        background-color: $gray-scale-color05;
        grid-area: 1 / 2 / 3 / 3;
        margin: auto;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .contacts-container h2 {
        margin: 1rem auto;
        font-size: 2.5rem;
        grid-area: 1 / 1 / 2 / 2;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
        width: 75%;
        text-align: center;
    }

    .social-media-buttons {
        width: 90%;
        height: calc(10% - 1rem);
        margin: 0.5rem auto;
        background-color: $black;
        grid-area: 3 / 2 / 4 / 3;
        display: flex;
        align-items: center;
        justify-content: space-around;
        border-radius: 0.5rem;
    }
}



.contacts-banner {
    grid-area: 2 / 1 / 4 / 2;
    background: url(../images/header-banner.png);
    transform: scaleX(-1);
    background-repeat: no-repeat;
    background-size: cover;
}

.form-contact-container h3 {
    width: 100%;
    font-size: 2rem;
    padding: 1rem;
    text-align: center;
}

.form-contact-container p {
    padding: 1rem;
    height: 33%;
}

.contact-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 75%;
    margin: auto;
}

.contact-form input[type=email] {
    width: 70%;
    background-color: $white;
    border: none;
    padding: 1rem;
    border-radius: 0.25rem;
    color: var(--black);
}

.contact-form button[type=submit] {
    width: 20%;
    background-color: $gray-scale-color04;
    border: none;
    padding: 1rem;
    border-radius: 0.25rem;
}



.social-media-button {
    width: calc(100% / 5);
    height: 100%;
    min-width: 3rem;
    min-height: 3rem;
    font-size: 2.5rem;
    transition: background-color 0.4s ease;
    display: flex;
    cursor: pointer;
}

.social-media-button i {
    margin: auto;
    cursor: pointer;
}

.social-media-button:first-child {
    border-radius: 0.5rem 0 0 0.5rem;
}

.social-media-button:last-child {
    border-radius: 0 0.5rem 0.5rem 0;
}

#facebook-button {
    background-color: $facebook-btn;
}

#instagram-button {
    background-color: $instagram-btn;
}

#whatsapp-button {
    background-color: $whatsapp-btn;
}

#twitter-button {
    background-color: $twitter-btn;
}

#linkedin-button {
    background-color: $linkdin-btn;
}

#facebook-button:hover {
    background-color: $facebook-hbtn;
}

#instagram-button:hover {
    background-color: $instagram-hbtn;
}

#whatsapp-button:hover {
    background-color: $whatsapp-hbtn;
}

#twitter-button:hover {
    background-color: $twitter-hbtn;
}

#linkedin-button:hover {
    background-color: $linkdin-hbtn;
}