@import 'colours';

@media (min-width: 600px) {
    .gallery-container {
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 0.5fr repeat(2, 1fr) 0.75fr;
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        align-items: center;
    }

    .gallery-description {
        height: 100%;
        margin-bottom: auto;
        grid-area: 2 / 1 / 5 / 2;
    }

    .gallery-img {
        width: 100%;
        height: 100%;
        grid-area: 1 / 2 / 4 / 3;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .img-selector {
        height: 100%;
        grid-area: 4 / 2 / 5 / 3;
    }

    .img {
        min-width: calc((100% / 3) - 0.8rem);
    }
}
@media (max-width: 600px) or (orientation: portrait) {
    .gallery-container {
        display: flex;
        flex-direction: column;
    }

    .gallery-description {
        height: 40%;
        margin: 1rem 0;
    }

    .gallery-img {
        margin: 1rem 0;
        width: 100%;
        height: 50%;
        box-shadow: 0px 5px 5px 0px #00000088;
    }

    .img-selector {
        height: 20%;
    }

    .img {
        min-width: calc((100% / 2) - 0.8rem);
    }
}

.gallery-container {
    width: 100%;
    height: 100%;
    padding: 1rem;
    box-shadow: 0px 5px 5px 0px #00000088;
}

.gallery-title {
    grid-area: 1 / 1 / 2 / 2;
    font-size: 3.5rem;
    text-align: center;
    border-top: 3px solid #000;
    border-bottom: 3px solid #000;
}

.gallery-button {
    width: 10%;
    height: 100%;
    background-color: $gray-scale-color03;
    border: none;
    transition: background-color 0.4s ease;
    cursor: pointer;
}

.gallery-button:hover {
    background-color: $gray-scale-color05;
}

#img-container {
    width: 80%;
    height: 100%;
    display: flex;
    overflow-x: scroll;
}

#img-container::-webkit-scrollbar {
    height: 5px;
}

#img-container::-webkit-scrollbar-track {
    background: $white;
}

#img-container::-webkit-scrollbar-thumb {
    background: $gray-scale-color03;
}

#img-container::-webkit-scrollbar-thumb:hover {
    background: $gray-scale-color05;
}

.gallery-description {
    width: 100%;
    padding: 1rem;
    background-color: $gray-scale-color04;
    box-shadow: 0px 5px 5px 0px #00000088;
}

.img-selector {
    width: 100%;
    background-color: $gray-scale-color04;
    padding: 0.25rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px 5px 5px 0px #00000088;
}

.img {
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    cursor: pointer;
    transition: opacity 0.4s ease;
    margin: 0 0.4rem;
}

.img:hover {
    opacity: 0.5;
}