@import 'colours';

.header-body {
    background: url(../images/header-bg-img.jpg);
    width: 100%;
    height: 100%;
    padding-top: $header-height;
    box-shadow: 0px 5px 5px 0px #00000088;
}

.blur {
    width: 100%;
    height: 100%;
    background-color: #ffffff44;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-item {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    padding: 1.5rem;
}

@media (min-width: 600px) {
    .blur {
        flex-direction: row;
    }

    .header-item {
        width: 45%;
    }

    #banner-header {
        width: 50%;
    }

    .principal-title {
        font-size: 3rem;
    }

    .name-title {

        h1, h2 {
            margin-left: 1rem;
        }

        h1 {
            font-size: 3.5rem;
        }

        h2 {
            font-size: 1.5rem;
        }
    }
}

@media (max-width: 600px) or (orientation: portrait) {
    .blur {
        flex-direction: column;
    }

    .header-item {
        width: 100%;
    }

    #banner-header {
        width: 100%;
    }

    .name-title {
    
        h1, h2 {
            text-align: center;
        }
        
        h1 {
            font-size: 3.5rem;
        }
    
        h2 {
            font-size: 2rem;
        }
    }
}

.contacts-container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr 4fr 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    padding-top: 1rem;
}

.name-title {
    border-top: 3px solid #000;
    border-bottom: 3px solid #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#banner-header {
    background: url(../images/header-banner.png);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    height: 100%;
}